@import url(https://fonts.googleapis.com/css?family=Quicksand|Work+Sans:700);
@import url(https://fonts.googleapis.com/css?family=Roboto|Staatliches);
body {
  background: #ecbeb4;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Section {
  text-align: center;
  color: #274e54;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-left: 10%;
  margin-right: 10%;
  padding: 5px;
}
h1 {
  font-family: 'Staatliches', cursive;
  cursor: pointer;
}
p {
  font-family: 'Roboto', sans-serif;
}
#main {
  background: #ecbeb4;
}
#main h1 {
  font-size: 5em;
}
#avocados {
  background: #519e8a;
}
#this {
  background: #c5c9a4;
}
#contact {
  background: #8bba7c;
}

